import React, {Component} from 'react';
import {HashRouter as Router, Switch, Route, Link} from "react-router-dom";
import {Navbar, Nav, Container, FormControl, Form, Button} from 'react-bootstrap';
import logo from '../images/bsu.png';
import search from '../images/search.png';
// import Home from '../pages/Home';
// import About from '../pages/About';
// import Contact from '../pages/Contact';
// import Homework from '../pages/Homework';
import img from '../images/logo.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import Footer from '../Components/AppFooter';
import email from '../images/email.png';
import phone from '../images/phone.png';
import location from '../images/location.png';
import { Row, Col} from 'react-bootstrap';
import AppHomework from '../Components/AppHomework';

class AppMenu extends Component {
  render(){
    return (
      <>
      <Router basename={process.env.PUBLIC_URL}>
      <Navbar fixed="top" collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="/">
            <img
              src={logo}
              height="40"
              width="40"
              className="d-inline-block align-top"
              alt="Logo"
            /> WP Homework
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav" >
            <Nav className="mr-auto">
              <Link to="/">HOME</Link>
              <Link to="/homework">HOMEWORK</Link>
              <Link to="/about">ABOUT</Link>
              <Link to="/contacts">CONTACTS</Link>
            </Nav>
            <Form inline >
              <FormControl
                type="text"
                placeholder="search"
                className="mr-sm-2"
              />
              <Button variant="outline-info" variant="dark">
                <img
                  src={search}
                  height="30"
                  width="30"
                  alt="Logo"
                  />
              </Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>


        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/homework" component={Homework} />
          <Route path="/about" component={About} />
          <Route path="/contacts" component={Contact} />
        </Switch>
      </Router>
      </>
    );
  }
}
const Home = () => <div className="App-home">
  <img src={img} className="App-logo" alt="logo" />
    <p>
    <Button variant="outline-primary" href="/homework">
      PROBLEMS
    </Button>
    </p>
    </div>

const About = () =>   <div class="App-about">
    <h1>О САЙТЕ</h1>
    <p class="App-about-p">
      Сайт был создан Чикалинским Никитой как финальный
      проект по веб программированию и представляет
      собой информацию о домашних заданиях для
      студентов БГУ, механико-математического факультета,
      1 курса 2 семестра. Сайт содержит
      задания и ссылки на дополнительные
      источники для изучения.
    </p>
    <Footer/>
  </div>

const Homework = () =>       <div>
        <AppHomework/>
      </div>


const Contact = () =>   <div class="App-contacts">
  <Container fluid="md">
      <Row className="justify-content-md-center">
        <Col xs="3" ><img height="40" src={email}/></Col>
        <Col style={{height: '50px' }}>
          <a
            className="App-link"
            target="_blank"
            href="https://mmf.bsu.by/ru/cathedras/web-technologies-and-computer-modeling/employees-wtcm/barvenov-sergej-aleksandrovich/"
           >Bars@bsu.by</a>
        </Col>
      </Row>
      <Row></Row>
      <Row className="justify-content-md-center">
        <Col xs="3"><img height="40" src={phone}/></Col>
        <Col style={{height: '50px' }}><a href="tel:+375292095361">(8 017) 209-53-61</a></Col>
      </Row>
      <Row className="justify-content-md-center">
        <Col xs="3"><img height="40" src={location}/></Col>
        <Col style={{height: '50px' }}>пр. Независимости 4, к. 423</Col>
      </Row>
      <h2 class="App-email-h2">Оставить отзыв</h2>
    <div style={{width: '50%'}}>

      <Form onSubmit={this.handleChange}>
        <Form.Group controlId="formBasicEmail">

          <Form.Label for="email">Email</Form.Label>
          <Form.Control
            type="email"
            name="email"
            onChange={this.handleChange}
            placeholder="Enter Email"/>
        </Form.Group>

        <Form.Group controlId="formBasicPassword">
          <Form.Label>Text</Form.Label>
          <Form.Control
            as="textarea"
            name="message "
            onChange={this.handleChange}
            rows="3"/>
        </Form.Group>
        <Button variant="outline-primary">SEND</Button>
      </Form>
    </div>
  </Container>
</div>
export default AppMenu;
